.section-7 {
    padding: 70px 0;
    background-color: rgba($color: #BAEAFB, $alpha: .14);

    .headline {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 50px;

        h2 {
            text-align: center;
        }
        p {
            text-align: center;
            width: 60%;
            @media only screen and (max-width: 992px) {
                width: 90%;
            }
        }
    }

    .recent-posts-container {
        width: 100%;
        padding-top: 70px;
        display: flex;
        flex-direction: row;
    }
}

.section-8 {
    position: relative;
    padding: 70px 0;
    padding-bottom: 210px;
    background-color: rgba($color: #BAEAFB, $alpha: .14);

    .headline {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 50px;

        p {
            text-align: center;
            width: 60%;

            @media only screen and (max-width: 992px) {
                width: 90%;
            }
        }

        h3 {
            font-weight: 600;
        }
    }
}
        
@keyframes movingPositionAnimation {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-30px);
    }
    100% {
        transform: translateY(0);
    }
}